<template>
  <div class="my-lawler">
    <v-container>
      <v-row class="text-center bg-primary-gradient">
        <v-col cols="12">
          <v-img
              :src="require('../../assets/logo-white.png')"
              contain
              height="120"
          />
        </v-col>

        <v-col cols="12" align-self="center">
          <h1 style="font-size: 1.5rem" class="font-weight-bold profile-title text-center">Documentos en línea</h1>
        </v-col>
      </v-row>

      <v-row class="breadcrumbs">
        <v-col cols="12">Categoría > Subcategoría > Tipo de pago</v-col>
      </v-row>

      <v-row>
        <v-col>
          <div class="page-title text-capitalize">{{ link.name }}</div>

          <div v-html="link.description"></div>

          <coupon
              :price="link.price"
              @code-used="onCodeUsed"
              @price-updated="onPriceUpdated"
          ></coupon>

          <p class="text-center">¿Cómo quieres pagar?</p>

          <div v-if="false" class="call-to-action" @click="goToPayment()">
            <div class="icon">
              <v-img :src="require('../../assets/icons/lawyer-05.svg')" contain height="24"/>
            </div>
            <div class="text">Pago con tarjeta</div>
          </div>

          <div class="call-to-action" @click="goToBank()">
            <div class="icon">
              <v-img :src="require('../../assets/icons/lawyer-06.svg')" contain height="24"/>
            </div>
            <div class="text">Depósito bancario</div>
          </div>

        </v-col>
      </v-row>
    </v-container>
  </div>
</template>

<script>
import {mapActions, mapGetters} from "vuex";
import axios from "axios";
import Coupon from "../../components/Coupon";

export default {
  name: 'DocumentsDetail',
  components: {Coupon},
  data: () => ({
    category: '',
    subcategory: '',
    link: '',
    items: []
  }),
  computed: {
    ...mapGetters(['user']),
  },
  mounted() {
    this.loadDocuments()
  },
  methods: {
    ...mapActions(['addCodeToOperation', 'addPriceToOperation']),

    loadDocuments() {
      let url = 'categories?type=document'
      axios.get(url).then((response) => {
        let id = this.$route.params.id
        let id2 = this.$route.params.id2
        let id3 = this.$route.params.id3

        this.category = response.data.find(obj => parseInt(obj.id) === parseInt(id))
        this.subcategory = this.category.subcategories.find(obj => parseInt(obj.id) === parseInt(id2))
        this.link = this.subcategory.links.find(obj => parseInt(obj.id) === parseInt(id3))
      })
    },
    onCodeUsed(code) {
      this.code = code
      this.addCodeToOperation(code)
    },
    onPriceUpdated(price) {
      this.priceTotal = price
      this.addPriceToOperation(price)
    },
    goToPayment() {
      this.$router.push({name: 'DocumentsPayment'})
    },
    goToBank() {
      this.$router.push({name: 'DocumentsBank'})
    },
  }
}
</script>
