<template>
  <v-container>
    <v-row>
      <v-col cols="12">
        <p class="text-center">Ingresa tú códígo de descuento</p>
      </v-col>
      <v-col cols="12">
        <v-row>
          <v-col cols="6">
            <v-text-field v-model="code" label="Codigo" outlined dense hide-details></v-text-field>
          </v-col>
          <v-col cols="6">
            <v-btn @click="onSubmit" :loading="isLoading" color="primary" block>Aplicar código</v-btn>
          </v-col>
        </v-row>
      </v-col>
      <v-col cols="12">
        <p class="text-center" style="margin-top: 1rem">Precio total: S/. {{ priceWithCoupon }}</p>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
import {mapGetters} from 'vuex'
import axios from 'axios'

export default {
  name: 'Coupon',
  inject: ['notyf'],
  props: ['price'],
  data: () => ({
    isLoading: false,
    code: '',
    discount: 0,
  }),
  computed: {
    ...mapGetters(['user']),
    priceWithCoupon() {
      let price = parseFloat(this.discount > 0 ? (1 - this.discount / 100) * this.price : this.price)
      console.log(price)

      return price.toFixed(2)
    }
  },
  methods: {
    onSubmit() {
      this.isLoading = true
      axios.get('/coupons/' + this.code).then((response) => {
        this.discount = parseInt(response.data.data.discount)

        this.$emit('code-used', this.code)
        this.$emit('price-updated', this.priceWithCoupon)

        this.notyf.success('Codigo de descuento aplicado!');
      }).catch(() => {
        this.notyf.error('Codigo no valido!');
      }).then(() => {
        this.isLoading = false
      })
    }
  }
}
</script>
